<template>
  <section>
    <b-navbar :showLeft="true" :title="DirectionPanel.start +' → '+ DirectionPanel.end"></b-navbar>

    <section class="main-info-container">
      <section class="site-title-container">{{DirectionPanel.start +' → '+ DirectionPanel.end}}</section>

      <section class="lines-container">
        <template v-for="(item,$index) in DirectionPanel.lines">
          <span :key="$index">{{item}}</span>
        </template>
      </section>

      <section class="time-stops-container">
        <span>{{DirectionPanel.totalTimes}} 分钟</span>
        <span>{{DirectionPanel.totalStops}} 站</span>
      </section>
    </section>

    <section class="accordion-container">
      <yd-accordion>
        <template v-for="(item,$index) in DirectionPanel.curSLines">
          <yd-accordion-item :key="$index" class="accordion-item-container">
            <section slot="title" style="font-size: .24rem">
              {{item.sname +' → '+ item.ename +'（'+ item.lname +'）'}}
            </section>
            <section>
              <section class="step-container">
                <section class="step-item-container">
                  <section class="step-item-left">
                    <yd-icon name="qidian" size=".6rem" color="#62B500" custom></yd-icon>
                  </section>
                  <i class="dot start-step"></i>
                  <section class="step-item-right">{{item.sname}}</section>
                </section>

                <template v-for="(station,$index) in item.stations">
                  <section class="step-item-container" :key="$index">
                    <section class="step-item-left"></section>
                    <i class="dot"></i>
                    <section class="step-item-right">{{station.name}}</section>
                  </section>
                </template>

                <section class="step-item-container">
                  <section class="step-item-left">
                    <yd-icon name="zhongdian" size=".6rem" color="#F54336" custom></yd-icon>
                  </section>
                  <i class="dot end-step"></i>
                  <section class="step-item-right">{{item.ename}}</section>
                </section>
              </section>
            </section>
          </yd-accordion-item>
        </template>
      </yd-accordion>

    </section>
  </section>
</template>

<script type="text/ecmascript-6">
  import BNavbar from '@/components/basis/BNavbar'
  import {mapGetters} from 'vuex'

  export default {
    name: 'Result',
    computed: {
      ...mapGetters([
        'DirectionPanel'
      ])
    },
    components: {
      BNavbar
    },
    created () {
      this.$nextTick(() => {
        if (!this.DirectionPanel) {
          this.$router.go(-1)
        }
      })
    }
  }
</script>

<style type="text/css" scoped>

  .accordion-container >>> .accordion-item-container:not(:last-child) {
    /*border-bottom: 1px dashed #efefef;*/
  }

  .accordion-container {
    margin-top: .2rem;
  }

  .main-info-container >>> .time-stops-container span:not(:last-child):after {
    content: '|';
    padding-left: .1rem;
    padding-right: .1rem;
  }

  .main-info-container >>> .time-stops-container {
    margin-top: .2rem;
    color: #909399;
  }

  .main-info-container >>> .lines-container span:not(:last-child):after {
    content: '→';
    padding-left: .1rem;
    padding-right: .1rem;
  }

  .main-info-container >>> .lines-container {
    margin-top: .2rem;
  }

  .main-info-container >>> .site-title-container {
    font-size: .28rem;
    font-weight: bold;

  }

  .main-info-container {
    min-height: 2rem;
    padding: .3rem .3rem .1rem .3rem;
    background-color: #ffffff;
    border-bottom: 1px solid #dedede;
    box-shadow: 0rem .05rem .2rem #dedede;
  }
</style>



